import React, { useState } from "react";
import "./Signup.css";
import axios from "axios";
import { BASE_URL } from "../../CONFIG";
import { useHistory } from "react-router";

const Signup = () => {
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [passwrodError, setPasswordError] = useState(false);
  const [error, setError] = useState();

  const history = useHistory();

  const signup = (e) => {
    e.preventDefault();
    setError(null);
    setPasswordError(false);
    if (password.trim() !== confirmPassword.trim()) {
      setPasswordError(true);
      return;
    }
    const data = {
      username,
      email,
      password,
      last_name: "",
      first_name: "",
    };
    axios
      .post(`${BASE_URL}/api/user/`, data)
      .then(() => {
        history.push("/wondaland");
      })
      .catch((error) => {
        setError(error.response.data);
      });
  };

  return (
    <div className="Signup">
      <h1>Create an account</h1>
      <p>
        There is a lot happening aroung you, enjoy it all in your comfort with
        your smart mobile phones in virtual reality
      </p>
      <form onSubmit={signup}>
        {error?.username[0] ? (
          <p style={{ color: "tomato" }}>
            user with the username already exists
          </p>
        ) : null}
        <input
          required
          placeholder="Username here"
          onChange={(e) => setUsername(e.target.value)}
          value={username}
        />
        {error?.email[0] ? (
          <p style={{ color: "tomato" }}>user with the email already exist</p>
        ) : null}
        <input
          required
          placeholder="Email Address here"
          onChange={(e) => setEmail(e.target.value)}
          value={email}
        />
        <input
          required
          placeholder="- Enter Password here -"
          onChange={(e) => setPassword(e.target.value)}
          value={password}
          type="password"
        />
        {passwrodError ? (
          <p style={{ color: "tomato" }}>password do not match!</p>
        ) : null}
        <input
          required
          placeholder="- Confirm Password -"
          onChange={(e) => setConfirmPassword(e.target.value)}
          value={confirmPassword}
          type="password"
        />
        <button type="submit">Sign Up</button>
      </form>
    </div>
  );
};

export default Signup;
