import React, { useState } from "react";
import axios from "axios";
import { BASE_URL } from "../../CONFIG";
import { useHistory } from "react-router";

const Login = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(false);

  const history = useHistory();

  const login = (e) => {
    e.preventDefault();
    setError(false);
    const data = {
      username,
      password
    };
    axios
      .post(`${BASE_URL}/api/login/`, data)
      .then(() => {
        history.push("/wondaland");
      })
      .catch((error) => {
        console.log(error.response);
        setError(true);
        // setError(error.response.message);
      });
  };

  return (
    <div className="Signup">
      <h1>Welcome Back!!</h1>
      <p>
        There is a lot happening aroung you, enjoy it all in your comfort with
        your smart mobile phones in virtual reality
      </p>
      <form onSubmit={login}>
        {error ? (
          <p style={{ color: "tomato" }}>Username or password is incorrect, try again</p>
        ) : null}
        <input
          required
          placeholder="Username here"
          onChange={(e) => setUsername(e.target.value)}
          value={username}
        />
        <input
          required
          placeholder="- Enter Password here -"
          onChange={(e) => setPassword(e.target.value)}
          value={password}
          type="password"
        />
        <button type="submit">Log In</button>
      </form>
    </div>
  );
};

export default Login;
